'use strict';

var React = require('react');

var consts = require('../../agent/consts');

function previewComplex(data, theme) {
  var style = {
    color: theme.special04
  };

  if (Array.isArray(data)) {
    return React.createElement("span", {
      style: style
    }, "Array[", data.length, "]");
  }

  switch (data[consts.type]) {
    case 'function':
      return React.createElement("span", {
        style: style
      }, data[consts.name] || 'fn', "()");

    case 'object':
      return React.createElement("span", {
        style: style
      }, data[consts.name] + '{…}');

    case 'date':
      return React.createElement("span", {
        style: style
      }, data[consts.name]);

    case 'symbol':
      return React.createElement("span", {
        style: style
      }, data[consts.name]);

    case 'iterator':
      return React.createElement("span", {
        style: style
      }, data[consts.name] + '(…)');

    case 'array_buffer':
    case 'data_view':
    case 'array':
    case 'typed_array':
      return React.createElement("span", {
        style: style
      }, "".concat(data[consts.name], "[").concat(data[consts.meta].length, "]"));

    case undefined:
    case null:
      return '{…}';
  }

  return null;
}

module.exports = previewComplex;